import { NextRequest, NextResponse } from "next/server"
import { optimizeForSEO } from "@/lib/seo-rush"
import { getSupabaseAdminClient } from "@/lib/supabase/server"
import { verifyAdmin } from "@/lib/admin-auth"

export async function POST(request: NextRequest) {
  try {
    const auth = await verifyAdmin()
    if (!auth.authorized) return auth.response

    const supabase = await getSupabaseAdminClient()
    let count = 0

    const { data: assets } = await supabase
      .from("assets")
      .select("id, title, description, features")
      .eq("status", "active")
      .limit(100)

    if (assets) {
      for (const asset of assets) {
        try {
          const seoData = await optimizeForSEO({
            title: asset.title,
            content: `${asset.title} ${asset.description} ${asset.features || ''}`,
            url: `${process.env.NEXTAUTH_URL}/asset/${asset.id}`,
          })

          await supabase
            .from("assets")
            .update({
              seo_title: seoData.title,
              seo_description: seoData.description,
              seo_keywords: seoData.keywords,
            })
            .eq("id", asset.id)

          count++
        } catch (error) {
          console.error(`Failed to optimize asset ${asset.id}`)
        }
      }
    }

    const { data: threads } = await supabase
      .from("forum_threads")
      .select("id, title, content")
      .eq("status", "approved")
      .limit(100)

    if (threads) {
      for (const thread of threads) {
        try {
          const seoData = await optimizeForSEO({
            title: thread.title,
            content: `${thread.title} ${thread.content}`,
            url: `${process.env.NEXTAUTH_URL}/forum/thread/${thread.id}`,
          })

          await supabase
            .from("forum_threads")
            .update({
              seo_title: seoData.title,
              seo_description: seoData.description,
              seo_keywords: seoData.keywords,
            })
            .eq("id", thread.id)

          count++
        } catch (error) {
          console.error(`Failed to optimize thread ${thread.id}`)
        }
      }
    }

    return NextResponse.json({ success: true, count })
  } catch (error) {
    return NextResponse.json({ error: "Bulk optimization failed" }, { status: 500 })
  }
}
